import React,{ Component } from 'react'
import Placeholder from './Placeholder'
import Date from './Date'
class Right_Form extends Component{
  render(){
  return(
    <div>
<div class="form">
    <Placeholder holdername={{title: "Contact Number",placehoder:"+91"}}/>
</div>
<br/>
<div class="form">
    <Date date={"Return Date"}/>
</div>


</div>
  );
}
}

export default Right_Form;
