import React,{ Component } from 'react'
import Placeholder from './Placeholder'
import Date from './Date'
class Middel_Form extends Component{
  render(){
  return(
    <div>
<div class="form">
    <Placeholder holdername={{title: "Your Name",placehoder:"Enter Your Name"}}/>
</div>
<br/>
<div class="form">
    <Date date={"Issue Date"}/>
</div>


</div>
  );
}
}

export default Middel_Form;
