import React,{ Component } from 'react'
import Left_side from "./Left_side"
import Middel_side from "./Middel_side"
import Right_side from "./Right_side"
import Title from "./Title"
import Middel_Form from "./Middel_Form"
import Right_Form from "./Right_Form"
import Btn from "./Btn"
import Logo from "./Logo"
class Div extends Component{
  render(){
    return(
      <div >
      <div class="container main">
          <div class="row no-gutters">
            <div class="col-sm no-gutters left" id="a">
                <Left_side/>
            </div>
            <div class="col-sm no-gutters btn left_line middel " id="b">
                <Title title={"Booking Details"}/>
                <Middel_Form/>
                <div id="back">
                <Btn  btn_name={"Back"}/>
                </div>
            </div>
            <div class="col-sm no-gutters btn left_line right">
            <div class="right_line " id="c">
            <Logo/>
            </div>
            <Right_Form/>
            <div class="right_line " id="d">
            <Btn  btn_name={"Book Car"}/>
            </div>
            </div>
          </div>
      </div>
      </div>
    );
  }
}
export default Div;
