import React,{ Component } from 'react'
class Placeholder extends Component{
  render(){
  return(
    <div>
    <label>
      {this.props.holdername.title}
    </label>
        <br/>
    <input type="text" placeholder={this.props.holdername.placehoder} required/>
    </div>
  );
}
}

export default Placeholder;
